.active__button,
.non-active__button {
  padding: 8px;
  outline: none;
  font-size: 12px;
  letter-spacing: 0.5px;
  font-weight: 700;
  color: white;
  border-radius: 5px;
  text-transform: uppercase;
  border: 0px solid;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.6);
  &:hover {
    border-bottom: 1px solid coral;
    padding-bottom: 7px;
    text-shadow: 0.5px -0.5px 3px coral;
  }
}

.active__button {
  color: coral;
}
